@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.tela-inicial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.logo{
    margin-top: 230px;
    width: 13%;
}
.iniciar{
    width: 25%;
    height: 72px;
    
    background-color: white;
    
    margin-top: 150px;
    border: 3px solid #FFEF61;
    border-radius: 12px;

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px 65px;
    grid-gap: 5px;
    gap: 5px;

    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
}
.iniciar:hover{
    cursor: pointer;
}
.titulo-carta#face-titulo{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;

    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
}
.botao-virar-carta{
    bottom: var(--distancia-carta);
    right: var(--distancia-carta);
    position: absolute;
}
.virar-carta{
    width: 49px;
}
:root{
    --distancia-carta: 25px;
}
.carta{
    width: 375px;
    height: 550px;

    border-radius: 12px;

    margin: 0 auto;
    margin-top: 150px;

    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;
    border-width: 3px;
    border-style: solid;
}
.contador-carta{
    margin-left: 50px;
    justify-self: flex-end;
    margin-right: 10px;

    position: absolute;
    right: 25px;
    right: var(--distancia-carta);
    top: 25px;
    top: var(--distancia-carta);
}
.titulo-carta{
    display: flex;
    align-items: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 14px;

    position: absolute;
    top: var(--distancia-carta);
    left: 50%;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
}
.conteudo-carta{
    height: 75%;
    width: 75%;

    display: flex;
    justify-content: center;
    align-items: center;

    text-align: justify;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.container-botoes{
    position: absolute;
    right: var(--distancia-carta);
    left: var(--distancia-carta);
    bottom: var(--distancia-carta);

    display: flex;
    justify-content: space-between;
}
.resposta{
    width: 75px;
    height: 58px;

    border-width: 3px;
    border-style: solid;
    box-sizing: border-box;
    border-radius: 12px;

    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
#aprendi-agora{
    border-color: #000000;
}
#nao-lembrei{
    border-color: #F74848;
}
#lembrei-esforco{
    border-color: #62DB38;
}
#zap{
    border-color: #FFEF61;
    font-weight: bold;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	background-color: transparent;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
strong {
	font-weight: bold;
}
*{
	box-sizing: border-box;
}
.cabecalho{
    padding: 10px;
}
.container{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.msg-principal{
    text-align: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
}
.texto-mensagem{
    text-align: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 26px;
}
html,
body,
.root{
    height: 100%;
}
